.btn_newContact,
.btn_send {
	margin: auto;
	max-width: 350px;
	border: none;
	outline: none;
	border-radius: 5px;
	background-color: #40d093;
	border: none;
	padding: 6px 15px;
	cursor: pointer;
}

.container_form_add {
	margin: auto;
	margin-bottom: 40px;
	background-color: #eee;
	border-radius: 10px;
	text-align: center;
	max-width: 450px;
}

.form_add {
	padding: 25px;
	padding-top: 5px;
}

.container__input {
	display: flex;
	margin: 8px 0;
}
.container__input input {
	margin-left: 5px;
}
