body {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	background-color: #3e2dd3;
	font-family: "Source Sans 3", sans-serif;
}

.title_page {
	text-align: center;
	color: #fff;
	margin-bottom: 40px;
}

.padre_contenedor {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	margin: auto;
	max-width: 1100px;
}

.contenedor {
	display: flex;
	justify-content: space-between;
	box-shadow: 0px 1px 3px #111;
	background-color: #d9d8d8;
	align-items: center;
	flex-direction: row;
	flex-wrap: nowrap;
	align-content: space-between;
}

.contenedor_info {
	display: flex;
	align-content: center;
	align-items: center;
}
.contenedor_info img {
	max-width: 75px;
	margin: 10px 20px;
}

.container_info-text {
	text-align: left;
}

.text_name {
	font-weight: bold;
	font-size: 1.3em;
}
.text_number {
	font-size: 1em;
}

.btn_newContact {
	text-align: center;
	font-weight: 600;
	display: flex;
	flex-direction: row;
	align-items: center;
}
.btn_newContact img {
	width: 2.1875rem;
	height: 2.1875rem;
	padding-left: 0.9375rem;
}
